 .Minigame {
        z-index: -1;
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        object-fit: contain;
}

.pinball-splash {
font-family: 'Nanum Pen Script', sans-serif;
position: absolute;
top: 0;
left: 0;
bottom: 0;
width: 100%;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
background-color: #000000d5;
}

.pinball-splash button {
        padding: 10px 20px;
        border-radius: 5px;
        color: white;
        font-family: 'Nanum Pen Script', sans-serif;
        font-size: x-large;
        /* font-weight: bolder; */
        background-color: rgb(187, 0, 0);
}
.pinball-splash div, button {
        margin: 20px 0;
}
    
.pinball-splash div {
        font-size: 60px;
        color: white;
}
    
.snake-container {
    text-align: center;
}

.game-border {
    box-shadow: 0px 0px 23px 0px rgba(0,0,0,0.75);
    margin: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
}

.point-bar {
    margin: 5px auto;
    text-align: right;
}

.game-splash {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #000000d5;
}

.game-splash div, button {
    margin: 20px 0;
}

.game-splash div {
    font-size: 60px;
    color: white;
}

.game-splash button {
    padding: 10px 20px;
    border-radius: 5px;
    color: white;
    font-family: 'Nanum Pen Script', sans-serif;
    font-size: x-large;
    /* font-weight: bolder; */
    background-color: rgb(187, 0, 0);
    transition: 0.5s;
}

.game-splash button:hover {
    background-color: white;
    cursor: pointer;
    color: black;
}

.snake-mobile-buttons {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.snake-mobile-buttons div {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
}

.snake-mobile-buttons div button {
    margin: 5px 100px;
    padding: 8px 20px;
    border-radius: 0;
    border: 1px solid black;
}
#pinball-container {
  z-index: -1;
  position: fixed;
  /* top: -200%;
    left: -200%; */
  height: 100%;
  width: 100%;
  object-fit: contain;
}

#pinball-container canvas {
  z-index: -1;
  position: fixed;
  top: -50%;
  left: -150%;
  height: 200%;
  width: 400%;
  object-fit: contain;
  border: solid black 3px;
}


#rascalCanvas { 
    z-index: -1;
    position: fixed;
    /* top: -180%; */
    left: -200%;
    height: 500%;
    width: 500%;
    object-fit: contain;
}

.carousel-track {
    min-height: 54px;
}

/* .onlytwo {
    margin-left: 34px;
} */

.carousel-item {
    text-align: center;
}

.carousel-item button {
    padding: 0;
    cursor: pointer;
    background-color: white;
    border: solid black 3px;
    border-radius: 50%;
    max-width: 50px;
    max-height: 50px;
    min-width: 50px;
    min-height: 50px;
}

.carousel-arrow {
    filter: grayscale(1);
    margin-top: 0
}

.equipped {
    background-color: green;
    /* color: rgb(0, 86, 136) */
}

#tab {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

#tab button {
    margin: 3px;
    padding: 0;
    color: black;
    font-family: 'Nanum Pen Script', sans-serif;
    font-size: larger;
    font-weight: bolder;
    border-bottom: solid black 3px;
    border-radius: 0;
    white-space: nowrap;
}

#store-content {
    background-color: gray;
    min-height: 45vh;
    max-height: 45vh;
    overflow-y: scroll
}

#bottom-tab {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    min-height: 50px
}

.coins {
    color: black;
    font-family: 'Courier New', Courier, monospace;
    font-size: x-large;
    font-weight: bolder;
    min-height: 30px;
    max-height: 30px;
}

.coins span {
    font-size: x-large;
    background-color: goldenrod;
    border-radius: 50%;
    padding: 0;
}

#done {
    color: white;
    font-family: 'Nanum Pen Script', sans-serif;
    font-size: x-large;
    /* font-weight: bolder; */
    background-color: rgb(187, 0, 0);
    min-height: 30px;
    max-height: 30px;
}

/*----------------------*/

.itemforsale {
    max-width: 90px;
    max-height: 65px;
    min-width: 90px;
    min-height: 65px;
}

.imgandtitle {
    display: flex;
    align-items: center;
    justify-content: left;
    flex-wrap: wrap;
}

.title {
    font-size: x-large;
    text-align: center;
    justify-content: space-around;
    min-width: 90px;
    white-space: nowrap;
}

.priceandpurchase {
    display: flex;
    justify-content: right;
    flex-wrap: wrap;
}

.price {
    color: black;
    font-family: 'Courier New', Courier, monospace;
    font-size: x-large;
    font-weight: bolder;
    min-height: 30px;
    max-height: 30px;
    text-align: right;
    margin-right: 10px;
    width: 90px
}

.price span {
    font-size: x-large;
    background-color: goldenrod;
    border-radius: 50%;
    padding: 0;
}

#purchase {/*For some reason class/className doesnt work for this one, only id*/
    color: rgb(0, 73, 0);
    background-color: rgb(34, 199, 19);
    font-family: 'Nanum Pen Script', sans-serif;
    font-size: x-large;
    font-weight: bolder;
    border: solid black 3px;
    min-height: 30px;
    max-height: 30px;
    margin-right: 5px;
}

#clickbait {
    color: black;
    align-self: center;
    font-family: 'Nanum Pen Script', sans-serif;
    font-size: xxx-large;
    font-weight: bolder;
    min-height: 50px;
    max-height: 50px;
    text-align: center;
    margin-bottom: 20px;
    width: 160px;
    border: solid black 3px;
}

#cents {
    font-size: x-large;
    background-color: goldenrod;
    border-radius: 50%;
    padding: 0;
}

.alreadyowned {
    display: flex;
    font-size: 50px;
    text-align: center;
    align-items: center;
    opacity: 0.75;
    line-height: 0.8;
}

.alreadyowned span {
    font-weight: bolder;
}
